import React, { Component } from 'react'
import Desktop from './navbar/Desktop'

import MediaQuery from 'react-responsive'
import Mobile from './navbar/Mobile'

import HashChange from 'react-hashchange'

const links = [
    {
        title: "Home",
        link: "home"
    },
    {
        title: "Portfolio",
        link: "portfolio"
    },
    {
        title: "Blog",
        link: "blog"
    },
    {
        title: "Contact",
        link: "contact"
    },
    {
        title: "An Empty Page",
        link: "empty"
    },
]

export default class NavBar extends Component {
    state = {
        hash: ''
    }

    componentDidMount() {
        this.setState({hash: window.location.hash.slice(2)})
    }

    render() {
        return (
            <div>
                <HashChange onChange={({ hash }) => this.setState({hash: hash.slice(2)})} />
                <MediaQuery minWidth={700}>
                    <Desktop links={links} location={this.state.hash}/>
                </MediaQuery>
                <MediaQuery maxWidth={700}>
                    <Mobile links={links} location={this.state.hash}/> 
                </MediaQuery>
            </div>
        )
    }
}
