import React, { Component } from 'react'
import LabelContainer from './contact/LabelContainer'

import './css/contact/contact.css'

const emotes = require('ascii-faces')

export default class Contact extends Component {
    render() {
        return (
            <div className="contact-container">
                <div className="contact-info-container">
                    <p>If you need something, <br /> always you can <strong>send me a mail</strong>.</p>
                    <LabelContainer link="mailto:omaredu.02@gmail.com" color="#F8054D" label="omaredu.02@gmail.com" />
                </div>
                <div className="emote-field">
                    <p>{emotes()}</p>
                    <h2>Let's talk brooo!</h2>
                </div>
            </div>
        )
    }
}
