import React, { Component } from 'react'

import '../css/contact/label.css'

export default class LabelContainer extends Component {

    render() {
        let { label, link, color } = this.props

        return (
            <div className="label-contact-link" style={{background: color}}>
                <a href={link}><p>{label}</p></a>
            </div>
        )
    }
}
